import { useContext, useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { UserContext } from '../Contexts';
import '../css/InsertUser.css'; 

function UserCreation() {
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const { user } = useContext(UserContext);

    const handleSubmit = async (e) => {
        e.preventDefault();

        const formData = {
            name: e.target.username.value,
            password: e.target.password.value,
            description: e.target.description.value,
            id_role: e.target.role.value
        };

        try {
            const response = await fetch(`http://localhost:5000/user_by_name/${formData.name}`, {
                method: "PUT",
                headers: {
                    'Content-type': 'application/json',
                    'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
                },
                body: JSON.stringify(formData)
            });

            if (response.ok) {
                navigate("/users");
            } else {
                setError(<p className="error-msg">Cannot create user</p>);
            }
        } catch (error) {
            console.error("Error:", error);
            setError(<p className="error-msg">An error occurred. Please try again later.</p>);
        }
    }

    return (
        <div className="user-creation-container">
            <form className="user-creation-form" onSubmit={handleSubmit} method="post">
                <h2>User Creation</h2>
                <div className="input-group">
                    <label htmlFor="username">Username</label>
                    <input name="username" type="text" pattern="[A-Za-z0-9]{1,28}" required />
                </div>
                <div className="input-group">
                    <label htmlFor="password">Password</label>
                    <input name="password" type="password" pattern="[A-Za-z0-9]{1,28}" required />
                </div>
                <div className="input-group">
                    <label htmlFor="description">Description</label>
                    <input name="description" type="text" pattern="[A-Za-z0-9 ]{1,255}" required />
                </div>
                <div className="input-group">
                    <label htmlFor="role">Role</label>
                    <input name="role" type="number" min="0" max="1" required />
                </div>
                <button type="submit">Submit</button>
                {error}
                <Link to="/home" className="go-home-link">Go Home</Link>
            </form>
        </div>
    );
}

export default UserCreation;