import { useContext, useState, useEffect } from 'react';
import { useNavigate, Link, useParams } from 'react-router-dom';
import { UserContext } from '../Contexts';
import '../css/UpdateUser.css'; 

function UserUpdate() {
    const navigate = useNavigate();
    const { id } = useParams();
    const [error, setError] = useState(null);
    const { user, setUser } = useContext(UserContext);
    const [formData, setFormData] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();

        const updatedData = {
            id: id,
            name: e.target.username.value,
            description: e.target.description.value
        };

        try {
            const response = await fetch(`http://localhost:5000/user/${id}`, {
                method: "PATCH",
                headers: {
                    'Content-type': 'application/json',
                    'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
                },
                body: JSON.stringify(updatedData)
            });

            if (response.ok) {
                if (id == user.id) {
                  setUser({...user, name: e.target.username.value});
                }
                navigate("/users");
            } else {
                setError(<p className="error-msg">Cannot update user</p>);
            }
        } catch (error) {
            console.error("Error:", error);
            setError(<p className="error-msg">An error occurred. Please try again later.</p>);
        }
    }

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch(`http://localhost:5000/user/${id}`, {
                    method: "GET",
                    headers: {
                        'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
                    }
                });

                if (response.ok) {
                    const new_data = await response.json();
                    setFormData(
                        <form className="update-form" onSubmit={handleSubmit}>
                            <label>Username</label><br />
                            <input name="username" defaultValue={new_data.name} type="text" required />
                            <br />
                            <label>Description</label><br />
                            <input name="description" defaultValue={new_data.description} type="text" required />
                            <br />
                            <button type="submit">Submit</button>
                        </form>
                    );
                } else {
                    setError(<p className="error-msg">Permission denied</p>);
                }
            } catch (error) {
                console.error("Error:", error);
                setError(<p className="error-msg">An error occurred. Please try again later.</p>);
            }
        }

        fetchData();
    }, [id, user]);

    return (
        <div className="update-container">
            <div className="update-form-container">
                {formData}
                {error}
                <Link to="/home" className="go-home-link">Go Home</Link>
            </div>
        </div>
    );
}

export default UserUpdate;
