import { useContext, useState, useEffect } from 'react';
import { useNavigate, Link, useParams } from 'react-router-dom';
import { UserContext, RolesContext } from '../Contexts';
import '../css/User.css';

function UserProfile() {
    const navigate = useNavigate();
    const { id } = useParams();
    const [error, setError] = useState(null);
    const { user } = useContext(UserContext);
    const [userData, setUserData] = useState(null);
    const [isAdminOrOwner, setIsAdminOrOwner] = useState(false);
    const roles = useContext(RolesContext);

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch(`http://localhost:5000/user/${id}`, {
                    method: "GET",
                    headers: {
                        'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
                    }
                });

                if (response.ok) {
                    const data = await response.json();
                    setUserData(data);
                    if (user.id_role === roles.admin || user.id === data.id) {
                        setIsAdminOrOwner(true);
                    }
                } else {
                    setError(<p className="error-msg">Permission denied</p>);
                }
            } catch (error) {
                console.error("Error:", error);
                setError(<p className="error-msg">An error occurred. Please try again later.</p>);
            }
        }

        fetchData();
    }, [id, user, roles.admin]);

    return (
        <div className="user-profile-container">
            <div className="user-details">
                <h2>User Profile:</h2>
                {userData && (
                    <div>
                        <h3>Username: {userData.name}</h3>
                        <p>Description: {userData.description}</p>
                        <p>Role: {userData.id_role}</p>
                    </div>
                )}
                {isAdminOrOwner && (
                    <div className="admin-options">
                        <button onClick={() => navigate(`/user/update/${id}`)}>Update</button>
                        <button onClick={() => navigate(`/user/delete/${id}`)}>Delete</button>
                    </div>
                )}
                <Link to="/home" className="go-home-link">Go Home</Link>
                {error}
            </div>
        </div>
    );
}

export default UserProfile;