import { useContext, useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { UserContext, RolesContext } from '../Contexts';
import '../css/Users.css';

function Login() {
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const { user } = useContext(UserContext);
    const [admin, setAdmin] = useState(false);
    const [users, setUsers] = useState([]);
    const roles = useContext(RolesContext);

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch("http://localhost:5000/users", {
                    method: "GET",
                    headers: {
                        'Authorization': 'Basic ' + btoa(`${user.name}:${user.password}`)
                    }
                });

                if (response.ok) {
                    const data = await response.json();
                    setUsers(data);
                    if (user.id_role === roles.admin) {
                        setAdmin(true);
                    }
                } else {
                    setError(<p className="error-msg">Permission denied</p>);
                }
            } catch (error) {
                console.error("Error:", error);
                setError(<p className="error-msg">An error occurred. Please try again later.</p>);
            }
        }

        fetchData();
    }, [user, roles.admin]);

    return (
        <div className="login-container">
            <div className="user-list">
                <h2>Users:</h2>
                {admin && <button className="add-user-btn" onClick={() => navigate("/user/insert")}>Add User</button>}
                <ul>
                    {users.map(user => (
                        <li key={user.id}>
                            <Link to={`/user/${user.id}`}>{user.name}</Link>
                        </li>
                    ))}
                </ul>
                <Link to="/home" className="go-home-link">Go Home</Link>
                {error}
            </div>
        </div>
    );
}

export default Login;